:root {
  --font: "Baloo Tamma 2", cursive;
  --main-colour: #96c6e0;
  --secondary-colour: #67acc7;
  --gradient-colour: linear-gradient(to top, #4b778f, #6c95ad);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: var(--font);
}

body {
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}
body::after {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.7);
  z-index: -1;
}

h2 {
  font-size: 3rem;
  color: var(--main-colour);
}
@media (max-width: 700px) {
  h2 {
    font-size: 2rem;
  }
}

p {
  font-size: 2rem;
  font-weight: 400;
  color: var(--main-colour);
}
@media (max-width: 700px) {
  p {
    font-size: 1.7rem;
  }
}

.container {
  width: 100%;
  height: 10vh;
  display: flex;
  justify-content: space-around;
  align-items: center;
  z-index: 999;
}
.container label {
  font-size: 2rem;
  color: var(--main-colour);
}
@media (max-width: 700px) {
  .container label {
    display: none;
  }
}
.container input {
  font-size: 1.5rem;
  outline: none;
  font-family: var(--font);
  padding: 0 0.3rem;
  color: var(--main-colour);
  border: none;
  text-align: center;
  border-radius: 1rem;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
}
@media (max-width: 700px) {
  .container input {
    width: 9rem;
    font-size: 1.2rem;
  }
}
.container .btn {
  text-align: center;
  border-radius: 1rem;
  color: var(--main-colour);
  padding: 0 0.6rem;
  font-size: 1.5rem;
  background-color: white;
  outline: none;
  border: none;
  cursor: pointer;
  background: transparent;
  transition: all 0.3s ease-in-out;
}
@media (max-width: 700px) {
  .container .btn {
    font-size: 1.2rem;
  }
}
.container .btn:hover {
  background: var(--secondary-colour);
  color: white;
}

.info {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  height: 90vh;
  width: 100vw;
  position: relative;
}

.icon {
  width: 5rem;
  height: 5rem;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.weather {
  text-transform: capitalize;
}